@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:"";content:none}q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

*{font-family:"Roboto", sans-serif}.wrapper{max-width:1920px;min-height:100vh;margin:0 auto;padding:0 60px 10px}.active{pointer-events:none}

.search-label{position:relative}.search-label .search-input{font-weight:400;font-size:48px;color:#b2b2b2;max-width:600px;height:80px;border:none;border-radius:20px;box-shadow:0px 2px 4px rgba(0,0,0,0.25);background:#ffffff;padding:12px 0 12px 32px}.search-label .search-input:active{color:#000}.search-label .search-icon{position:absolute;top:30%;right:35px}

.header{display:flex;justify-content:space-between;align-items:center;margin-top:0;padding:100px 50px 60px 50px;font-weight:700}.header .header-link{max-width:550px;height:200px;display:flex;align-items:center;text-decoration:none;color:#000}.header .header-link .link_text{font-size:72px}

.table-row{display:grid;grid-template-columns:50px 2.5fr 1fr;font-size:24px;padding:0 30px;border-radius:20px;margin-bottom:20px;box-shadow:0px 2px 4px rgba(0,0,0,0.25);height:80px;line-height:80px;font-weight:700;cursor:pointer}.table-row p{border-left:1px solid #b2b2b2;padding:0 20px}.table-row p:first-child{border:none;padding:0}.table-row:first-child p{border-left:1px solid #fff}.table-row:first-child p:first-child{border:none}.table-header{background-color:#2196f3;color:#ffffff}

.popUp{position:fixed;margin:auto;top:0;left:0;bottom:0;right:0;width:600px;height:400px;padding:20px;color:#666666;font-weight:400;border-radius:20px;box-shadow:0 0 0 100vh rgba(0,0,0,0.5);background-color:#fff;z-index:99;pointer-events:auto}.popUp h2{color:#000;text-align:center;font-size:48px;margin-bottom:38px;font-weight:700}.popUp .popUp-row{display:grid;grid-template-columns:50px 1fr minmax(-webkit-max-content, 100px);grid-template-columns:50px 1fr minmax(max-content, 100px);height:40px;align-items:center;margin-bottom:36px;font-size:24px}.popUp .popUp-row>svg{justify-self:center}.popUp .popUp-row p{padding:0 20px}.popUp .popUp-row p:last-child{text-align:right}.popUp .btn-ok{cursor:pointer;width:171px;height:49px;border-radius:20px;border:none;text-transform:uppercase;color:#fff;font-weight:700;font-size:24px;background-color:#2196f3;margin:0 214px}

